* {
  box-sizing: border-box;
  background-color: #faeab1;
  margin: 0;
}

#current-date {
  text-align: center;
  color: #533c1f;
  font-size: medium;
}

body {
  margin: 0;
  font-family: system-ui, sans-serif;
  font-size: 16px;
}

footer {
  text-align: center;
  background-color: #c58940;
  padding-top: 10px;
  padding-bottom: 10px;
}

footer img {
  width: 100px;
  height: auto;
}

footer p {
  color: #fff;
  background-color: #c58940;
  margin-top: 1px;
  margin-bottom: 10px;
  font-size: xx-small;
  display: block;
}

nav ul {
  flex-direction: column;
  justify-content: center;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

nav img {
  background-color: #faf8f1;
  width: 50%;
  margin: 20% 20% 10% 23%;
}

#nav-menu {
  background-color: #faf8f1;
  width: 200px;
  height: 100vh;
  transition: left .5s;
  position: fixed;
  top: 0;
  left: -100%;
  box-shadow: 2px 0 100px #0000004d;
}

#hamburger {
  color: #fcfaee;
  cursor: pointer;
  text-align: left;
  text-shadow: none;
  z-index: 1000;
  background-color: #c58940;
  border: none;
  width: 100%;
  padding: 10px;
  font-size: 30px;
  transition: color .3s;
  display: block;
  position: fixed;
}

#hamburger:hover {
  color: #e5ba73;
}

#nav-menu.active {
  z-index: 100;
  left: 0;
}

#nav-menu li {
  text-align: center;
  background-color: #faf8f1;
  padding: 16px 0;
}

#nav-menu li a {
  color: #533c1f;
  background-color: #faf8f1;
  font-weight: 600;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

#nav-menu li a:hover {
  color: #e5ba73;
}

div .box {
  background-color: #fff;
}

div .container {
  border: 2px dashed #e5ba73;
}

main {
  flex-direction: column;
  padding: 3rem;
  display: flex;
}

main .article {
  background-color: #fff;
  margin-bottom: 3rem;
  padding: 10px;
  transition: transform .5s;
  overflow: hidden;
}

main .article:hover {
  transform: scale(1.1);
}

h3 {
  text-align: center;
  margin: 0;
  padding: 100px 20px 20px;
  font-family: Georgia, serif;
  font-size: 30px;
}

main h1 {
  color: #fff;
  text-align: center;
  background-color: #e5ba73;
  margin-top: 0;
  margin-bottom: 3rem;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: larger;
}

main h2 {
  color: #533c1f;
  background-color: #faf8f1;
  padding-top: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

main p {
  background-color: #faf8f1;
  padding-left: 1rem;
  padding-right: 1rem;
}

main img {
  border: 10px solid #faf8f1;
  width: 100%;
  height: auto;
}

main a {
  color: #7a5428;
  background-color: #faf8f1;
  border-bottom: 2px dashed #e5ba73;
  width: 100%;
  padding-bottom: 1rem;
  padding-left: 1rem;
  display: inline-block;
}

main a:hover {
  color: #faeab1;
}

@media only screen and (width >= 1200px) {
  #current-date {
    text-align: center;
    font-size: 20px;
  }

  h3 {
    margin-top: 10px;
    font-size: 60px;
  }

  div .box {
    background-color: #faf8f1;
  }

  div .container {
    border: 2px dashed #e5ba73;
    margin: 10px;
  }

  main {
    padding: 2rem 6rem 6rem;
  }

  main h1 {
    margin-bottom: 2rem;
    font-size: 20px;
  }

  main .article {
    background-color: #fff;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    margin-bottom: 1rem;
    transition: background-color .3s, box-shadow .3s;
  }

  main .article:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 20px #faeec4;
  }

  main img {
    border-radius: 0;
    width: 100%;
    height: auto;
  }

  #hamburger {
    color: #c58940;
    background-color: #0000;
    border: none;
    margin: 0;
    padding-left: 1rem;
    font-size: 40px;
  }

  #hamburger:hover {
    color: #e5ba73;
    text-shadow: none;
    width: fit-content;
  }

  #nav-menu {
    width: 300px;
  }

  #nav-menu li a {
    font-size: 20px;
    font-weight: 450;
  }

  #top-stories {
    columns: 4;
    background-color: #fff;
    column-gap: 1rem;
    width: 100%;
  }

  footer {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  footer p {
    font-size: medium;
  }
}
